.modalContainer {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  background-color: rgba(0, 0, 0, 0.5);

  display: flex;
  justify-content: center;
  align-items: center;
}

.modal {
  min-width: 200px;
  min-height: 100px;
  background-color: #21212b;
  position: fixed;
  border-radius: 30px;
  padding: 20px;
  display: flex;
  flex-direction: column;
}
